/* Base Containers */

.w-container,
.w-container-c,
.w-container-w {
    position: relative;
    display: flex;
    box-sizing: border-box;
    z-index: 3;
}

/* Flex Modifiers */
.w-container-w {
    flex-wrap: wrap;
}
.w-container-c,
.w-container-small-c {
    flex-direction: column;
}

/* Width Variants */
.w-full,
.w-fluid {
    display: flex;
    width: 100vw;
}
.w-fluid {
    padding-left: 1rem;
    padding-right: 1rem;
}

/* Fixed Width Containers */
.w-container,
.w-container-c,
.w-container-nf {
    width: 1200px;
    min-width: 1200px;
    margin: 0 auto;
    padding: 0 35px;
}

.w-container-small,
.w-container-small-c,
.w-container-small-nf {
    width: 900px;
    min-width: 900px;
    margin: 0 auto;
}

.w-container-small {
    display: flex;
}

/* Gap Utilities */
.jgp-large {
    gap: 40px;
}
.jgp-collapse {
    gap: 0;
}

/* Grid Widths */
.w-1-1 { width: 100%; }
.w-1-2, .w-2-4 { width: 50%; }
.w-1-3 { width: 33.3333%; }
.w-2-3 { width: 66.6667%; }
.w-1-4 { width: 25%; }
.w-3-4 { width: 75%; }
.w-1-5 { width: 20%; }
.w-2-5 { width: 40%; }
.w-3-5 { width: 60%; }
.w-4-5 { width: 80%; }
.w-1-6 { width: 16.6667%; }
.w-2-6 { width: 33.3333%; }
.w-3-6 { width: 50%; }
.w-4-6 { width: 66.6667%; }
.w-5-6 { width: 83.3333%; }

/* Children Width Component (wc-1-*) */
[class*="wc-1-"].w-container > * {
    width: 100%;
}

.wc-1-2 > *:first-child { width: 50%; }
.wc-1-3 > *:first-child { width: 33.3333%; }
.wc-1-4 > *:first-child { width: 25%; }
.wc-1-5 > *:first-child { width: 20%; }
.wc-1-6 > *:first-child { width: 16.6667%; }

/* With Gap - calc(width - gap/2) */
.wc-1-2-g1 > *:first-child { width: calc(50% - 0.5rem); }
.wc-1-3-g1 > *:first-child { width: calc(33.3333% - 1rem); }
.wc-1-4-g1 > *:first-child { width: calc(25% - 1.5rem); }
.wc-1-5-g1 > *:first-child { width: calc(20% - 2rem); }
.wc-1-6-g1 > *:first-child { width: calc(16.6667% - 2.5rem); }

/* Responsive Modifiers */
@media (min-width: 768px) {
    .wc-1-2\@m > *:first-child { width: 50%; }
    .wc-1-3\@m > *:first-child { width: 33.3333%; }
    .wc-1-4\@m > *:first-child { width: 25%; }
    .wc-1-5\@m > *:first-child { width: 20%; }
    .wc-1-6\@m > *:first-child { width: 16.6667%; }
}
@media (min-width: 960px) {
    .wc-1-2\@l > *:first-child { width: 50%; }
    .wc-1-3\@l > *:first-child { width: 33.3333%; }
    .wc-1-4\@l > *:first-child { width: 25%; }
    .wc-1-5\@l > *:first-child { width: 20%; }
    .wc-1-6\@l > *:first-child { width: 16.6667%; }
}

